import { decorate, observable, runInAction} from "mobx";
import axios from "axios";

class Store {
  constructor(rootStore) {
    this.rootStore = rootStore
  }
  popularUpdates = {
    list:[],
    isLoading:true,
    fetch:()=>this._fetch("popularUpdates","?sort=date&take=6")
  }
  updates = {
    list:[],
    isLoading:true,
    fetch:()=>this._fetch("updates","/updates?sort=date")
  }
  created = {
    list:[],
    isLoading:true,
    fetch:()=>this._fetch("created","?sort=create&take=7")
  }
  news = {
    text:'',
    isLoading:true,
    fetch:async()=>{
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/news`)
      return runInAction(() => {
        this.news.text=response.data.text
        this.news.isLoading=false
      })
    }
  }  
  _fetch=async(subStoreName,apiEnd)=>{
    this[subStoreName].isLoading=true
    this[subStoreName].list=[]
    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/manga${apiEnd}`)
   
      return runInAction(() => {
        this[subStoreName].list=response.data.list
        this[subStoreName].isLoading=false
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  }
}
Store = decorate(Store, {
  news:observable,
  updates:observable,
  created:observable,
  popularUpdates:observable,
})

export default Store;