import { decorate, observable, action} from "mobx";

import MangaFilterStore from './mangaFilter.store'
import MangaListStore from './mangaList.store'
import ChapterStore from './chapter.store'
import MangaStore from './manga.store'
import AuthStore from './auth.store'
import UserStore from './user.store'
import CommentStore from './comment.store'
import ChatStore from './chat.store'
import MainPageStore from './mainPage.store'

class RootStore {
  constructor () {
    this.UserStore = new UserStore(this)
    this.AuthStore = new AuthStore(this)
    this.MangaStore = new MangaStore(this)
    this.ChapterStore = new ChapterStore(this)
    this.CommentStore = new CommentStore(this)
    this.MangaFilterStore = new MangaFilterStore(this)
    this.MangaListStore = new MangaListStore(this,'manga')
    this.MangaListUpdatesStore = new MangaListStore(this,'manga/updates')
    this.ChatStore = new ChatStore(this)
    this.MainPageStore = new MainPageStore(this)
    this.theme = !!localStorage.dark ? 'dark' : 'light'
    // setInterval(()=>console.log(this.UserStore.user.followers[0]?this.UserStore.user.followers[0].status:"----"),1000)
  }
  changeTheme(dark) {
    this.theme = dark ? 'dark' : 'light'
  }
}
RootStore = decorate(RootStore, {
  theme:observable,
  changeTheme:action
})

export default RootStore;