import { decorate, observable, runInAction, action } from "mobx";
import axios from "axios";
import sortParams from '../sort.params'
class Store {
  constructor(rootStore,apiEndpoint) {
    this.rootStore = rootStore
    this.apiEndpoint = apiEndpoint
    this.sortBy=sortParams[0].value
    this.page=1
    this.total=0
    this.list=[]
    this.isLoading=false
  }
  setSortBy(value){
    this.sortBy=value
    this.fetchMangaChank()
  }
  get cnt(){
    return this.list.length
  }
  get hasMore(){
    return this.cnt!==this.total
  }
  clear=()=>{
    this.page=1
    this.total=0
    this.list=[]
  }
  fetchMangaChank=async(isFirst=true)=>{

    if(!isFirst&&(!this.hasMore||this.isLoading)){
      return
    }
    if(isFirst){
      this.list=[]
    }
    this.isLoading=true
    const filterOption = this.rootStore.MangaFilterStore.option
    try {
      const params = {
        sort:this.sortBy,
        dir:"DESC",
        ...filterOption,
        skip:isFirst?0:(this.page-1)*24,
        take:24
      }
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/${this.apiEndpoint}`,{params});

      runInAction(() => {
        this.list=isFirst?response.data.list:[...this.list,...response.data.list]
        this.total=response.data.total
        this.page=isFirst?2:this.page+1
        this.isLoading=false
      })
    } catch (e) {
      runInAction(() => {
        this.isLoading=false
      })
      console.log(`Axios request failed: ${e}`);
    }
  }
  nextMangaChank=()=>this.fetchMangaChank(false)
}
Store = decorate(Store, {
  list:observable,
  clear:action
})

export default Store;