
import CircularProgress from '@material-ui/core/CircularProgress';
import React,{Suspense} from 'react';
import Container from '@material-ui/core/Container';
function LazyComponent({Component,...props}) {
    return (
      <div>
        <Suspense fallback={<div style={{position:'absolute',top:'calc(50vh - 20px)',left:'calc(50vw - 20px)'}}><CircularProgress color="secondary" /></div>}>
            <Component {...props}/>
        </Suspense>
      </div>
    );
  }

  function Lazy(Component,contain=true) {
    if(contain){
      return props=><Container fixed><LazyComponent {...props} Component={Component}/></Container>;
    }
    return props=><LazyComponent {...props} Component={Component}/>;
  } 

  function LazyComponentSubPage({Component,...props}) {
    return (
      <div>
        <Suspense fallback={<div style={{paddingLeft:'calc(50% - 10px)',paddingTop:40}}><CircularProgress color="secondary" /></div>}>
          <Component {...props}/>
        </Suspense>
      </div>
    );
  }
  function LazySub(Component) {
    return props=><LazyComponentSubPage {...props} Component={Component}/>;
  } 
const MainPage = Lazy(React.lazy(() => import('../Pages/MainPage')));
const UserPage = Lazy(React.lazy(() => import('../Pages/UserPage')));
const MangaPage = Lazy(React.lazy(() => import('../Pages/MangaPage')),false);
const MangaListPage = Lazy(React.lazy(() => import('../Pages/MangaListPage')));
const MangaReadPage = Lazy(React.lazy(() => import('../Pages/MangaReadPage')));
const MangaUpdatePage = Lazy(React.lazy(() => import('../Pages/MangaUpdatePage')));
const CreateMangaPage = Lazy(React.lazy(() => import('../Pages/CreateMangaPage')));
const CreateChapterPage = Lazy(React.lazy(() => import('../Pages/CreateChapterPage')));
const SignIn = Lazy(React.lazy(() => import('../Pages/SignInPage')));
const SignUp = Lazy(React.lazy(() => import('../Pages/SignUpPage')));
const Chat = Lazy(React.lazy(() => import('../Pages/Chat')));
const LikedSubPage = LazySub(React.lazy(() => import('../Pages/UserPage/SubPage/liked')));
const HistorySubPage = LazySub(React.lazy(() => import('../Pages/UserPage/SubPage/history')));
const FriendsSubPage = LazySub(React.lazy(() => import('../Pages/UserPage/SubPage/friends')));
const EditProfilePage = Lazy(React.lazy(() => import('../Pages/EditProfilePage')));

// React.lazy(() => import('./OtherComponent'));
export const RoutesWithoutNav = {
    ReadManga:{ path:'/manga/serie-:mangaId/chapter-:chapterId', link:(mangaId,chapterId)=>`/manga/serie-${mangaId}/chapter-${chapterId}`, component:MangaReadPage, exact:true },

}

export const RoutesWithNav = {
    Main:{ ruName:'интересое', path:'/', component:MainPage, exact:true },
    MangaList:{ ruName:'каталог', path:'/manga', component:MangaListPage, exact:true },
    MangaUpdate:{ ruName:'обновления',  path:'/updates', component:MangaUpdatePage, exact:true },
    Chat:{ ruName:'диалоги', path:'/chat', component:Chat, exact:true },
    Manga:{ path:'/manga/serie-:mangaId', link:mangaId=>`/manga/serie-${mangaId}`, component:MangaPage, exact:true },
    User:{ path:['/user/profile-:userId'], link:userId=>`/user/profile-${userId}`,component:UserPage, exact:true,
        tabList:[
            {label:"Понравившиеся",subLink:'',component:LikedSubPage},
            {label:"История",subLink:'history',component:HistorySubPage},
            {label:"Друзья",subLink:'friends',component:FriendsSubPage},
        ]
    },
    CreateManga:{ ruName:'Добавление новой манги', path:'/create-manga', component:CreateMangaPage, exact:true },
    CreateChapter:{ path:'/manga/serie-:mangaId/create-chapter', link:mangaId=>`/manga/serie-${mangaId}/create-chapter`, component:CreateChapterPage, exact:true },
    SignIn:{ path:'/login', component:SignIn, exact:true },
    SignUp:{ path:'/registration', component:SignUp, exact:true },
    EditProfilePage:{ path:'/user/edit-profile', component:EditProfilePage, exact:true },
}
RoutesWithNav.User.tabList.map(t=>{t.path=RoutesWithNav.User.path+'/'+t.subLink})
RoutesWithNav.User.path=[RoutesWithNav.User.path,...RoutesWithNav.User.tabList.map(t=>t.path)]

export const NavRouteList = Object.values(RoutesWithNav)
export const Routes = {...RoutesWithNav,...RoutesWithoutNav}
export const RouteList = Object.values(Routes)
export const TabRouteList = RouteList.filter(route=>route.ruName)


