import { decorate, observable, runInAction} from "mobx";
import axios from "axios";

class Store {
  constructor(rootStore) {
    this.rootStore = rootStore
  }
  tree=[]
  adjacencyList={0:[]}
  isLoading=true
  fetch=async(mangaId)=>{
    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/comments/${mangaId}`)
     
      const list=response.data
      const adjacencyList={0:[]}
      list.map(el=>{
        let parentId = 0
        if(el.rootPath.length>0){
          parentId = el.rootPath[el.rootPath.length-1]
          if(!adjacencyList[parentId]){
            adjacencyList[parentId]=[]
          }
        }
        return adjacencyList[parentId].push(el)
      })
     
      return runInAction(() => {
        this.adjacencyList=adjacencyList
        this.isLoading=false
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  }
  setComment=async({mangaId,comment,rootId})=>{
    try {
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/comments`,{mangaId,rootId,comment})
    
      const data = {...this.rootStore.AuthStore.current_user,...response.data}
      
      return runInAction(() => {
        this.adjacencyList[rootId||0]=this.adjacencyList[rootId||0]
        ?[...this.adjacencyList[rootId||0],data]
        :[data]
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  }
  setVote=async({commentId,value,comment})=>{
    let afterValue = comment.value
    if(comment.usersRated[0]){
      afterValue-=comment.usersRated[0].value
    }
    afterValue+=value
   
    try {
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/comments/vote`,{commentId,value})
      
      // const data = {...this.rootStore.AuthStore.current_user,...response.data}
   
      return runInAction(() => {
        comment.usersRated=[response.data]
        comment.value=afterValue
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  }
}
Store = decorate(Store, {
  adjacencyList:observable,
  isLoading:observable,
})

export default Store;