import { decorate, observable, runInAction} from "mobx";
import axios from "axios";
class Store {
  constructor(rootStore) {
    this.rootStore = rootStore
  }
  isLoading=true
  user={id:null,followers:[]}
  fetchUser = async userId => {
    this.isLoading=true

    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/user/${userId}`)

      return runInAction(() => {
        this.user=response.data 
        this.isLoading=false
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }  
  }
  addFriend = async() => {
    try {
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/friend`,{ reseiverId:this.user.id })
     
      if(response.data.id){
        return runInAction(() => {
          this.user.followers[0]=response.data 
        })
      }
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }   
  }
  deleteFriend = async() => {
    try {
      const response = await axios.delete(`${process.env.REACT_APP_API_BASE}/friend`,{ data: { reseiverId:this.user.id } })
   
      if(response.status===200){
        return runInAction(() => {
          this.user.followers=[]
        })
      }
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }   
  }
  setImg = async (file,type) => {
    // const file = e.target.files[0]
 
    let formData = new FormData();
    formData.set('file', file);
    try {
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/upload/${type}Img`, formData)
     
      return runInAction(() => {
        this.user={
          ...this.user,
          [`${type==='avatar'?'img':type}Url`]:response.data.url
        }
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }   
    
  }
}

Store = decorate(Store, {
  user:observable,
  isLoading:observable,
  // access_token:observable,
  // manga:observable,
})

export default Store;