import { decorate, observable, action} from "mobx";
import filterParams from '../filter.params'

class Store {
  constructor(rootStore) {
    this.rootStore = rootStore
  }
  filter=filterParams
  keys=Object.keys(filterParams)
  cnt=0
  get option(){
    const activeOptions=this.keys.map(key=>this.filter[key].filter(el=>el.value!=0))
    const option={}
    activeOptions.map((activeOption,i)=>{
      if(this.keys[i]=='genre'){
        option.includeGenre=activeOption.filter(el=>el.value===1).map(el=>el.id)
        option.excludeGenre=activeOption.filter(el=>el.value===2).map(el=>el.id)
      } else {
        option[this.keys[i]]=activeOption.map(el=>el.id)
      }
    })
    return option
  }
  changeFilter=(type,index,max,storeName)=>{
    this.filter[type][index].value=this.filter[type][index].value===max?0:this.filter[type][index].value+1
    this.filter[type][index].value===0&&this.cnt--
    this.filter[type][index].value===1&&this.cnt++

    return this.rootStore[storeName].fetchMangaChank(this.option)
  }
  clearAll=(storeName)=>{
    this.filter=filterParams
    this.cnt=0
    return this.rootStore[storeName].fetchMangaChank({})
  }
}
Store = decorate(Store, {
  filter:observable,
  cnt:observable,
  clearAll:action,
  changeFilter:action
})

export default Store;