import { decorate, observable, action, runInAction} from "mobx";
import axios from "axios";

class Store {
  constructor(rootStore) {
    this.rootStore = rootStore
    this.authStore = rootStore.AuthStore

  }


  dialogs = []
  dialogsIsLoading = true
  dialogIsLoading = false
  selectedDialog = null
  messages = []
  userById = {}
  socket= null
  setDialog = id => {

    this.selectedDialog = id
    this.fetchMessages(id)
  }
  fetchDialogs = async () => {


    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/chat`)
    
      return runInAction(() => {
        this.dialogs=response.data.map(d=>{
          const user = d.users.find(u=>u.id!==this.authStore.current_user.id)
          return({
            ...d,
            user:user||this.authStore.current_user
          })
        })

        this.dialogsIsLoading=false
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }   
  }
  fetchMessages = async id => {
    this.dialogIsLoading=true
    this.messages = []

    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/chat/${id}`)

      return runInAction(() => {
        const obj = {}
        response.data.users.map(user=>{
          obj[user.id]=user
        })
        this.messages=response.data.messages.map(msg=>{
          msg.user=obj[msg.userId]
          return msg
        })
        this.userById={...this.userById,...obj}
        this.dialogIsLoading=false
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  }

  eventMassage = msg => {

    this.dialogs=this.dialogs.map(el=>{
      if(el.id===msg.chatId){
          return{
          ...el,
          text:msg.text,
          updateAt:msg.createAt
        }
      }
      return el
    })
    if(this.selectedDialog===msg.chatId){
      msg.user=this.userById[msg.userId]
    }
    this.messages=[...this.messages,msg]
  }
  sendMassage = async msg => {
  
    if(msg.text.length){
      window.socket.emit('message', msg)
    }
    
  }
}
Store = decorate(Store, {
  dialogIsLoading:observable,
  dialogsIsLoading:observable,
  dialogs:observable,
  selectedDialog:observable,
  messages:observable,
  setDialog:action,
  isLoading:observable,
  chapter:observable,
  eventMassage:action,
})

export default Store;