import { decorate, observable, autorun, runInAction} from "mobx";
import axios from "axios";
import openSocket from 'socket.io-client'
class Store {
  constructor(rootStore) {
    this.rootStore = rootStore
  }
  access_token = window.localStorage.getItem('token');
  current_user = null;
  fetchProfile=async()=>{
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/auth/profile`)

      return runInAction(() => {
        this.current_user=response.data
      })
  }
  tokenCheker = autorun(() => {
    if(this.access_token){

      localStorage.setItem("token", this.access_token)
      axios.defaults.headers.common = {'Authorization': `Bearer ${this.access_token}`};
      if(!window.socket){
        window.socket = openSocket(process.env.REACT_APP_SOCKET,{query:`jwtToken=${this.access_token}`});
        window.socket.on('message', (message) => {
      
          this.rootStore.ChatStore.eventMassage(message)
        })
      }
      if(this.current_user==null){

        this.fetchProfile()
      }
    } else {
      window.socket.disconnect()
      localStorage.removeItem("token")
      this.current_user=null
    }
  });



  login=async({username, password})=>{
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/auth/login`,{username, password})

      return runInAction(() => {
        localStorage.removeItem("token")
        this.access_token=response.data.access_token
        this.current_user=response.data.user
      })
  } 
  registration=async({username, email, password})=>{
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/auth/registration`,{username, email, password})

      return runInAction(() => {
        localStorage.removeItem("token")
        this.access_token=response.data.access_token
        this.current_user=response.data.user
      })
  } 
  logout=()=>{
    localStorage.removeItem("token")
    this.access_token=''
    this.current_user=null
  }
}

Store = decorate(Store, {
  current_user:observable,
  access_token:observable,
})

export default Store;