import React,{useEffect,useState,Suspense} from 'react';
import {BrowserRouter as Router, Switch, Route} from "react-router-dom";

import { ThemeProvider } from '@material-ui/core/styles';
import {NavRouteList,Routes} from './Routes';
import './App.css'
import { Provider } from 'mobx-react';
import RootStore from './Store';
import { observer } from 'mobx-react';
import CssBaseline from '@material-ui/core/CssBaseline';
import './index.css';
import * as serviceWorker from './serviceWorker';
import { createMuiTheme } from '@material-ui/core/styles';
import CircularProgress from '@material-ui/core/CircularProgress';
const ErrorPage = React.lazy(() => import('./Pages/ErrorPage'));
const UserCover = React.lazy(() => import('./Pages/UserPage/cover'));
const NavBar = React.lazy(() => import('./Components/navbar'));


serviceWorker.unregister();
function Lazy({Component,...props}) {
  return (
    <div>
      <Suspense fallback={<div style={{position:'absolute',top:'calc(50vh - 20px)',left:'calc(50vw - 20px)'}}><CircularProgress color="secondary" /></div>}>
        <Component {...props}/>
      </Suspense>
    </div>
  );
}
const shadow = {
  dark:'0, 0, 0, 0.12',
  light:'218,224,235,.6'
}
  const getTheme= (theme) => ({
      palette: {
        type: theme,
        primary: {
          main: '#8bc34a'//#03a9f4
        },
        secondary: {
          main: '#8bc34a'
        },
      },

      overrides: {
        MuiPaper: {
          boxShadow: `0 3px 12px 0 rgba(${shadow[theme]})`,

          elevation1: {
            boxShadow: `0 3px 12px 0 rgba(${shadow[theme]})`
          },
          elevation2: {
            boxShadow: `0 5px 26px 0 rgba(${shadow[theme]})`,
          },
          elevation3: {
            boxShadow: `0 8px 20px 0 rgba(${shadow[theme]})`,
          },
        }
      }
  })
const rootStore = new RootStore();
export default observer(() => {
  const [UITheme,setUITheme] = useState(getTheme(rootStore.theme))
  useEffect(()=>{
    setUITheme(getTheme(rootStore.theme))
  },[rootStore.theme])
  return (  
    <Provider 
    rootStore={rootStore}
    authStore={rootStore.AuthStore}
    mangaListUpdatesStore={rootStore.MangaListUpdatesStore}
    userStore={rootStore.UserStore}
    mangaStore={rootStore.MangaStore}
    chapterStore={rootStore.ChapterStore}
    mangaFilterStore={rootStore.MangaFilterStore}
    mangaListStore={rootStore.MangaListStore}
    mainPageStore={rootStore.MainPageStore}
    commentStore={rootStore.CommentStore}
    chatStore={rootStore.ChatStore}
  >
  <ThemeProvider theme={createMuiTheme(UITheme)}>
    <CssBaseline />
    <Router >
        <Switch>
          <Route exact path={NavRouteList.filter(R=>R.path!=='/admin').map(R=>R.path)} render={(props) => <Lazy Component={NavBar} {...props}/>}/>
        </Switch>
        <Switch>
          <Route path={Routes.User.path} render={(props) => <Lazy Component={UserCover} {...props}/>} />
        </Switch>
        
          <Switch>
  <Route path={Routes.ReadManga.path} render={(props) => <Lazy Component={Routes.ReadManga.component} {...props}/>} exact={Routes.ReadManga.exact} />
            {NavRouteList.map(params => <Route key={params.path} {...params}/>)}
            <Route exact path="**" render={(props) => <Lazy Component={ErrorPage} status={404} {...props}/>}/>
          </Switch>
       

    </Router>
  </ThemeProvider>
  </Provider>
)});
