import { decorate, observable, runInAction} from "mobx";
import axios from "axios";

class Store {
  constructor(rootStore) {
    this.rootStore = rootStore
  }
  manga = {id:null}
  isLoading = true
  
  fetchManga=async(mangaId)=>{
    this.isLoading=true
    try {
      const response = await axios.get(`${process.env.REACT_APP_API_BASE}/manga/${mangaId}`)
   
      return runInAction(() => {
        this.manga=response.data
        this.isLoading=false
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  }
  setRating=async(mangaId,value)=>{
    try {
   
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/manga/rating`,{ id:mangaId, value })
 
      return runInAction(() => {
        this.manga={...this.manga,...response.data}
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  }
  subscribe=async(mangaId)=>{
    try {

      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/manga/subscribe`,{ mangaId })

      return runInAction(() => {
        this.manga.subscribe=true
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  }
  unsubscribe=async(mangaId)=>{
    try {
 
      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/manga/unsubscribe`,{ mangaId })
   
      return runInAction(() => {
        this.manga.subscribe=false
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  } 
  setBookmark=async({mangaId,chapterId})=>{
    try {

      const response = await axios.post(`${process.env.REACT_APP_API_BASE}/manga/bookmark`,{ mangaId,chapterId })
    
      return runInAction(() => {
        this.manga.bookmark=response.data
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  } 
  deleteBookmark=async({mangaId})=>{
    try {

      const response = await axios.delete(`${process.env.REACT_APP_API_BASE}/manga/bookmark`,{ data:{mangaId} })
    
      return runInAction(() => {
        delete(this.manga.bookmark)
      })
    } catch (e) {
      console.log(`😱 Axios request failed: ${e}`);
    }
  } 
}
Store = decorate(Store, {
  isLoading:observable,
  manga:observable,
})

export default Store;